.logIn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logInBtn {
  width: 100%;
  height: 4rem;
  background: linear-gradient(275.76deg, #7158e2 44.33%, #cd84f1 98.56%);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  color: #ffff;
  outline: none;
  border: none;
  margin: auto;
  cursor: pointer;
  margin-top: 3rem;
  font-size: 1.6rem;
  letter-spacing: 0.1rem;
  transition: all 0.3s;

  &:hover {
    background: #ffffff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    color: #7158e2;
    transform: translateY(-0.2rem);
  }
}

.switcher {
  text-align: center;
  margin-top: 2rem;
  &:hover {
      color:#7158e2;
      cursor: pointer;
  }
}

.logInText {
  font-size: 2rem;
  text-align: center;
  color: #414b56;
  font-weight: 600;
}



.errorHandler {
  text-align: center;
  color: red;
}
